<template>
    <div style="width: 100%; height: 115vh;">
        <div class="wrapper">
            <h2> </h2>
            <div class="item"> </div>
            <input class="input" type="text" placeholder=" " v-model="placeName" />
            <div class="item"> </div>
            <input class="file" id="file" type="file" accept="image/*" />
            <div class="item"></div>
            <input disabled class="input" type="text" id="address" placeholder="" v-model="address">
            <input class="btn" type="button" @click="searchAddress()" value=" ">
            <input class="input" type="text" id="detailAddress" placeholder="" v-model="detailAddress">
            <div class="item"> </div>
            <textarea class="textarea" placeholder="   " maxlength="250" v-model="placeSummary"/>
            <div class="item"> </div>
            <textarea class="textarea" placeholder="  " maxlength="250" v-model="placeIntro" />
            <div class="item"> </div>
            <textarea class="textarea" placeholder="   " maxlength="250" v-model="rule" />
            <button class="submit" @click="submit"></button>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue';
export default {
    setup() {
        const placeName = ref('');
        const address = ref('');
        const detailAddress = ref('');
        const placeSummary = ref('');
        const placeIntro = ref('');
        const rule = ref('');

        const searchAddress = () => {
            new daum.Postcode({
                oncomplete: function(data) {
                    //        .

                    //       .
                    //      ('') ,    .
                    var addr = ''; //  

                    //        .
                    if (data.userSelectedType === 'R') { //     
                        addr = data.roadAddress;
                    } else { //     (J)
                        addr = data.jibunAddress;
                    }

                    address.value = addr;
                    //    .
                    document.getElementById("detailAddress").focus();
                }
            }).open();
        }

        const submit = () => {
            if(localStorage.getItem('authority') == null) {
                alert('  ');
            } else if(localStorage.getItem('authority') === 'member') {
                if(placeName.value.length < 2) {
                    alert('    ');
                } else if(document.getElementById('file').files.length === 0) {
                    alert('    ');
                } else if(address.value === '') {
                    alert(' ');
                } else if(placeSummary.value.length < 10) {
                    alert('    ');
                } else if(placeIntro.value.length < 10) {
                    alert('    ');
                } else if(rule.value.length < 10) {
                    alert('    ');
                } else {
                    //
                }
            } else {
                alert(' ');
            }
        }

        return {
            placeName,
            address,
            detailAddress,
            placeSummary,
            placeIntro,
            rule,
            searchAddress,
            submit,
        }
    }
}
</script>

<style scoped>
.wrapper {
    width: 40%;
    height: 100%;
    margin: 0 auto;
}
.item {
    font-size: 1.3em;
    margin: 0.5em 0;
}
.file::file-selector-button {
    padding: 1em;
    background-color: #041461;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 1em;
    cursor: pointer;
}
.file::file-selector-button:hover {
    background-color:skyblue;
}
.btn {
    padding: 1em;
    background-color: #041461;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 1em;
    margin-left: 1em;
    cursor: pointer;
}
.btn:hover {
    background-color: skyblue;
}
.input {
    width: 60%;
    padding: 1em 1em 1em 1em;
    border:none;
    border-bottom: 1px solid gray;
}
.input:focus {
    outline: none;
    border-bottom: 1px solid black;
}
.textarea {
    width: 100%;
    height: 90px;
    font-family: Inter;
    resize: none;
    padding: 0.5em;
}
.submit {
    padding: 1em 3em;
    background-color: #041461;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 1em;
    margin-top: 2em;
    margin-left: 1em;
    cursor: pointer;
    float: right;
}
.submit:hover {
    background-color: skyblue;
}
</style>