<template>
  <div style="padding: 4.5%; height: 100vh">
    <h1 style="color: rgb(4, 20, 97, 1)"> </h1>
    <div style="width: 45%; float: left">
      <p class="inputExpl"> </p>
      <input
        class="resInput"
        type="text"
        placeholder="  "
      />
      <p class="inputExpl"> </p>
      <input
        class="resInput"
        type="tel"
        placeholder="  "
      />
      <button class="certiBtn"> </button>
      <p class="phoneCerti" v-show="correctNum">  </p>
      <p class="inputExpl"></p>
      <input type="text" placeholder=" " class="insertCerti" />
      <button class="certiDone"></button>
      <p class="payWay"> </p>
      <select class="how2pay">
        <option value="/" selected>/</option>
        <option value=" "> </option>
        <option value=". ">. </option>
      </select>
      <p>
        <input type="checkbox" v-model="allSelected" @click="selectAll" /><b
          ></b
        >
      </p>
      <p>
        <input type="checkbox" v-model="a" @click="selectA" /> 
        / <a style="color: red">()</a>
      </p>
      <p>
        <input type="checkbox" v-model="b" @click="selectB" />  
         <a style="color: red">()</a>
      </p>
      <p>
        <input type="checkbox" v-model="c" @click="selectC" />  3
         <a style="color: red">()</a>
      </p>
      <p>
        <input type="checkbox" v-model="d" @click="selectD" /> 14 
        <a style="color: red">()</a>
      </p>
    </div>
    <div
      style="width: 45%; float: right; background-color: #fafafa; padding: 3%"
    >
      <p style="margin-bottom: 1%"></p>
      <a style="font-size: 1.5rem">{{ resInfos.spaceName }} </a>
      <table class="typePriceTable" style="width: 90%">
        <tr>
          <th class="typePrice"></th>
          <th class="typePrice"></th>
        </tr>
        <tr>
          <td class="tdTypePrice">{{ resInfos.type }}</td>
          <td class="tdTypePrice" v-show="spaceType != ''">
            {{ price }}/
          </td>
          <td class="tdTypePrice" v-show="spaceType == ''">
            {{ price }}/
          </td>
        </tr>
      </table>
      <p style="margin-bottom: 1%">   </p>
      <select
        v-show="spaceType != ''"
        class="timeSelect"
        v-model="startSelected"
        @change="timeSelector"
      >
        <option selected disabled value=""></option>
        <option v-for="num in 24" :key="num">{{ time[num - 1] }}:00</option>
      </select>

      <div v-show="spaceType == ''">
        <button
          @click="showStartDates"
          style="background-color: white; border: 1px solid white"
        >
          
        </button>
        <a class="dayShow" v-show="showingStart">
          {{ startingDay }} 00:00</a
        >
        <div v-show="showingSTartDates" style="padding: 3%">
          <button
            v-for="num in rentDays"
            :key="num"
            class="resDate"
            @click="startDate"
            :value="startDates[num - 1]"
          >
            {{ startDates[num - 1] }}
          </button>
        </div>
      </div>

      <p style="margin-bottom: 1%">   </p>
      <button
        v-show="spaceType == ''"
        @click="showEndDates"
        style="background-color: white; border: 1px solid white"
      >
        
      </button>
      <a class="dayShow" v-show="showingEnd"> {{ endingDay }} 23:59 </a>
      <div v-show="showingEndDates" style="padding: 3%">
        <button
          v-for="num in rentDays"
          :key="num"
          class="resDate"
          @click="endDate"
          :value="endDates[num - 1]"
        >
          {{ endDates[num - 1] }}
        </button>
      </div>
      <select
        v-show="spaceType != '' && endTimeShow"
        class="timeSelect"
        v-model="endSelected"
        @change="showCoast"
      >
        <option selected disabled value=""></option>
        <option v-for="num1 in minusEndTime" :key="num1">
          {{ restTimes[num1 - 1] }}:00
        </option>
      </select>
      <hr />
      <div v-show="showTotalCoast">
        <p style="margin-bottom: 1.5%; font-size: 1.4rem">  </p>
        <p
          style="
            margin-bottom: 1.5%;
            font-size: 1.4rem;
            color: #d14a58;
            font-weight: bolder;
          "
        >
          {{ totalCoast }}
        </p>
        <label style="color: #4d6aed; font-weight: bolder"></label>
        <input
          type="number"
          v-model="mileage"
          style="width: 11%; margin-left: 2.5%; margin-right: 2.5%"
        />
        <button class="mileageBtn" @click="milCalcul">
          <a style="font-size: inherit"></a></button
        ><span style="font-size: 0.9rem; opacity: 0.4"
          >:{{ totalMileage }}</span
        >
        <ul v-show="spaceType != ''">
          <li>
                   ,   5%
            .
          </li>
          <li>     20%  .</li>
        </ul>
        <ul v-show="spaceType == ''">
          <li>   ,  .</li>
        </ul>
        <div v-show="spaceType != ''">
          <button class="nOfficePayBtn" @click="prePay"></button>
          <button class="nOfficePayBtn" @click="depositPay"> </button>
        </div>
        <div v-show="spaceType == ''">
          <button class="officePayBtn"> </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Datepicker from "vue3-datepicker";
import { useRoute } from "vue-router";
import { ref } from "vue";
import axios from "@/axios";
export default {
  setup() {
    const route = useRoute();
    const id = ref(route.query.id);

    const allSelected = ref(false);
    const a = ref(false);
    const b = ref(false);
    const c = ref(false);
    const d = ref(false);

    const time = ref([]);
    const restTimes = ref([]);

    const startDates = ref([]);
    const endDates = ref([]);

    const correctNum = ref(true);
    const showTotalCoast = ref(true);

    const resInfos = ref({
      //   breakClose: "",
      //   breakOpen: "",
      //   closeTime: "",
      //   mileage: 0,
      //   openTime: "",
      //   price: 0,
      //   reservedTime: [],
      //   spaceId: 0,
      //   spaceName: "",
      //   type: "",
    });

    const total = ref(0);

    const spaceType = ref("");
    const price = ref("");
    const totalCoast = ref("");

    const endTimeShow = ref(false);
    const minusEndTime = ref(0);
    const startSelected = ref("");
    const endSelected = ref("");
    const mileage = ref(0);
    const totalMileage = ref(0);
    let now = ref("");

    const showingSTartDates = ref(false);
    const showingEndDates = ref(false);
    const showingStart = ref(false);
    const showingEnd = ref(false);
    const startingDay = ref("");
    const endingDay = ref("");
    const rentDays = ref(30);

    const openTime = "";
    const closeTime = "";
    const breakOpen = "";
    const breakClose = "";

    // const dates = ref([]);
    // const yearMonth = ref("");
    // let dateBoard = ref("");

    const date = new Date();
    now = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;

    //   
    const getResInfos = async () => {
      const res = await axios.get(`reservation/details/${id.value}`, {
        headers: {
          Authorization: localStorage.getItem("access_token"),
        },
      });
      resInfos.value = { ...res.data };
      totalMileage.value = res.data.mileage;
      //   openTime = res.data.openTime;
      console.log(typeof res.data.openTime);
      //   closeTime = res.data.closeTime;
      //   breakOpen = res.data.breakOpen;
      //   breakClose = res.data.breakClose;
      //   console.log(resInfos.value);
      console.log(res.data);

      // 
      price.value = resInfos.value.price
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    };
    getResInfos();

    //   
    const selectAll = () => {
      // console.log(e.target);
      // if (e.target.value == "on") {
      // }
      allSelected.value = !allSelected.value;
      a.value = true;
      (b.value = true), (c.value = true), (d.value = true);
    };
    const selectA = () => {
      allSelected.value = false;
      a.value = !a.value;
      console.log(a.value);
    };
    const selectB = () => {
      allSelected.value = false;
      b.value = !b.value;
      console.log(b.value);
    };
    const selectC = () => {
      allSelected.value = false;
      c.value = !c.value;
      console.log(c.value);
    };
    const selectD = () => {
      allSelected.value = false;
      d.value = !d.value;
      console.log(d.value);
    };

    //  
    const timeMaker = () => {
      let t;
      for (let i = 1; i < 25; i++) {
        t = i;
        if (i < 10) {
          t = "0" + i;
        }
        time.value.push(t);
      }
      console.log(time.value);
    };
    timeMaker();

    const resTimeMaker = () => {
      // time.value.slice(resInfos.value.openTime.slice(0,2))
      console.log(resInfos.value);
    };
    resTimeMaker();

    //  
    const timeSelector = () => {
      endTimeShow.value = true;
      minusEndTime.value = 24 - parseInt(startSelected.value.slice(0, 2));
      restTimes.value = time.value.slice(
        parseInt(startSelected.value.slice(0, 2))
      );
    };

    // 
    const showCoast = () => {
      showTotalCoast.value = true;

      if (resInfos.value.type != "office") {
        const startTime = parseInt(startSelected.value.slice(0, 2));
        const endTime = parseInt(endSelected.value.slice(0, 2));

        total.value = (endTime - startTime) * resInfos.value.price;
      } else if (resInfos.value.type == "office") {
        //    
        const getDateDiff = (d1, d2) => {
          const date1 = new Date(d1);
          const date2 = new Date(d2);
          const diffDate = date1.getTime() - date2.getTime();
          return Math.abs(diffDate / (1000 * 60 * 60 * 24));
        };
        //db       
        total.value =
          getDateDiff(endingDay.value, startingDay.value) *
          resInfos.value.price;
      }
      totalCoast.value = total.value
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    };

    // 
    const milCalcul = () => {
      if (mileage.value > totalMileage.value) {
        alert("  ");
        mileage.value = 0;
        return;
      }
      totalMileage.value -= mileage.value;
      totalCoast.value = (total.value - mileage.value)
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    };

    // 
    const depositPay = async () => {
      IMP.init("imp76177137");
      IMP.request_pay(
        {
          pg: "kakaopay.TCSUBSCRIP",
          pay_method: "card", //  .
          merchant_uid: resInfos.value.merchant_uid, //    
          name: resInfos.value.spaceName,
          amount: totalCoast.value * 0.2, //    40  . price 20%  . // , 0 .
          customer_uid: localStorage.getItem("memberId"), //  
          buyer_email: "",
          buyer_name: "",
          buyer_tel: "",
        },
        function (rsp) {
          if (rsp.success) {
            axios.post("reserve/insert", {
              imp_uid: rsp.imp_uid,
              prepay_uid: rsp.merchant_uid,
              memberId: parseInt(localStorage.getItem("memberId")),
              companyId: resInfos.value.companyId,
              payStatus: "003",
              price: totalCoast.value,
              prepay: "001",
              spaceId: resInfos.value.spaceId,
              startDate: now + " " + startSelected.value,
              endDate: now + " " + endSelected.value,
              mileage: mileage.value,
            });
            alert(" .");
            window.location.reload(true);
          }
        }
      );
      // console.log({
      //   companyId: resInfos.value.companyId,
      //   spaceId: resInfos.value.spaceId,
      //   startDate: now + " " + startSelected.value,
      //   endDate: now + " " + endSelected.value,
      //   mileage: mileage.value,
      // });
    };

    //
    const prePay = async () => {
      IMP.init("imp76177137");
      IMP.request_pay(
        {
          pg: "kakaopay.TCSUBSCRIP",
          pay_method: "card", //  .
          merchant_uid: resInfos.value.merchant_uid, //    
          name: resInfos.value.spaceName,
          amount: totalCoast.value * 0.2, //    40  . price 20%  . // , 0 .
          customer_uid: localStorage.getItem("memberId"), //  
          buyer_email: "",
          buyer_name: "",
          buyer_tel: "",
        },
        function (rsp) {
          if (rsp.success) {
            axios.post("reserve/insert", {
              imp_uid: rsp.imp_uid,
              prepay_uid: rsp.merchant_uid,
              memberId: parseInt(localStorage.getItem("memberId")),
              companyId: resInfos.value.companyId,
              payStatus: "002",
              price: totalCoast.value,
              prepay: "000",
              spaceId: resInfos.value.spaceId,
              startDate: now + " " + startSelected.value,
              endDate: now + " " + endSelected.value,
              mileage: mileage.value,
            });
            alert(" .");
            window.location.reload(true);
          }
        }
      );
      // console.log({
      //   companyId: resInfos.value.companyId,
      //   spaceId: resInfos.value.spaceId,
      //   startDate: now + " " + startSelected.value,
      //   endDate: now + " " + endSelected.value,
      //   mileage: mileage.value,
      // });
    };

    //   
    const showingDates = () => {
      let currentYear = date.getFullYear();
      let currentMonth = date.getMonth() + 1;
      if (currentMonth > 12) {
        currentMonth = 1;
        currentYear++;
      }

      if (currentMonth < 10) {
        currentMonth = "0" + currentMonth;
      }

      let days = new Date(currentYear, currentMonth, 0).getDate();

      const today = date.getDate();
      let dates = [];

      //   
      for (let i = 1; i <= days; i++) {
        dates.push(i);
      }
      //   
      dates = dates.slice(today - 1);
      //30    30  
      for (let i = 1; i < 30; i++) {
        dates.push(i);
        //31     
        if (dates.length > 30) {
          dates.pop();
        }
      }
      //2   30
      if (days < 30) {
        console.log(1);
        //30   
        for (let j = 1; j + today == 30; j++) {
          dates.push(j);
        }
      }
      //    
      for (let i = 0; i < dates.length; i++) {
        //1   
        if (dates[i] === 1) {
          currentMonth++;
          // 12  1 
          if (currentMonth > 12) {
            currentMonth = 1;
            currentYear++;
          }
        }
        // 10  0
        if (dates[i] > 0 && dates[i] < 10) {
          dates[i] = `0${dates[i]}`;
          // dates[i] = dates[i].replace(1, "");
          // console.log(dates[i]);
        }
        startDates.value.push(`${currentYear}/${currentMonth}/${dates[i]}`);
        // startDates.value[0] = "";
      }
    };
    showingDates();

    //      
    const startDate = (e) => {
      showingStart.value = true;
      showingSTartDates.value = !showingSTartDates.value;
      startingDay.value = e.target.value;
    };
    const endDate = (e) => {
      showingEnd.value = true;
      showingEndDates.value = !showingEndDates.value;
      endingDay.value = e.target.value;
      showCoast();
    };

    //  
    const showStartDates = () => {
      showingSTartDates.value = !showingSTartDates.value;
      // if (startingDay.value) {
      //   startingDay.value = "";
      // }
      // console.log(startingDay.value);
    };

    //   
    const showEndDates = () => {
      showingEndDates.value = !showingEndDates.value;
      //   
      let day = parseInt(startingDay.value.slice(8, 10));
      let resYear = startingDay.value.slice(0, 4);
      let resMonth = startingDay.value.slice(5, 7);
      // if (resMonth > 12) {
      //   resMonth = 1;
      //   resYear++;
      // }

      let dates = [];
      let days = new Date(resYear, resMonth, 0).getDate();

      //dates    
      for (let i = 1; i <= days; i++) {
        dates.push(i);
      }
      // ()   
      dates = dates.slice(day);
      //    0   
      if (dates.length < 1) {
        resMonth++;
      }
      //    30  
      for (let i = 1; i < 31; i++) {
        dates.push(i);
        //31 30   
        if (dates.length > 30) {
          dates.pop();
        }
      }
      //2()
      if (days < 30) {
        //  30  
        for (let j = 1; j + day == 30; j++) {
          dates.push(j);
        }
      }
      //   
      for (let i = 0; i < dates.length; i++) {
        //1    12  1   
        if (dates[i] === 1 && dates[0] !== 1) {
          resMonth++;
          if (resMonth > 12) {
            resMonth = 1;
            resYear++;
          }
          //10    0
          if (resMonth < 10) {
            resMonth = "0" + resMonth;
          }
        }
        // 10  0
        if (dates[i] > 0 && dates[i] < 10) {
          dates[i] = `0${dates[i]}`;
        }
        //         1 
        if (dates[i] === 1 && dates[0] !== 1) {
          resMonth++;
        }
        endDates.value.push(`${resYear}/${resMonth}/${dates[i]}`);
      }
      console.log(day);
      console.log(dates.length);
    };
    // 
    const officePayBtn = () => {
      IMP.init("imp76177137");
      IMP.request_pay(
        {
          pg: "kakaopay.TCSUBSCRIP",
          pay_method: "card", //  .
          merchant_uid: resInfos.value.merchant_uid, //    
          name: resInfos.value.spaceName,
          amount: 0, //    40  . price 20%  . // , 0 .
          customer_uid: localStorage.getItem("memberId"), //  
          buyer_email: "",
          buyer_name: "",
          buyer_tel: "",
        },
        function (rsp) {
          if (rsp.success) {
            axios.post("reserve/insert", {
              imp_uid: rsp.imp_uid,
              prepay_uid: rsp.merchant_uid,
              memberId: parseInt(localStorage.getItem("memberId")),
              companyId: resInfos.value.companyId,
              payStatus: "001",
              price: totalCoast.value,
              prepay: "002",
              spaceId: resInfos.value.space.spaceId,
              startDate: startingDay.value + " " + "00:00",
              endDate: endingDay.valeu + " " + "23.59",
              mileage: mileage.value,
            });
            alert(" .");
            window.location.reload(true);
          }
        }
      );
    };

    return {
      total,
      showTotalCoast,
      showCoast,
      totalCoast,
      spaceType,
      price,
      restTimes,
      minusEndTime,
      timeMaker,
      correctNum,
      a,
      time,
      b,
      c,
      d,
      selectA,
      selectB,
      selectC,
      selectD,
      timeSelector,
      //   selectList,
      endTimeShow,
      startSelected,
      endSelected,
      allSelected,
      selectAll,
      resInfos,
      mileage,
      totalMileage,
      milCalcul,
      depositPay,
      prePay,
      // dates,
      // yearMonth,
      // dateBoard,
      startDates,
      endDates,
      startDate,
      endDate,
      showStartDates,
      showingSTartDates,
      showingEndDates,
      showingStart,
      showingEnd,
      startingDay,
      endingDay,
      rentDays,
      showEndDates,
      officePayBtn,
    };
  },
};
</script>

