// eslint-disable-next-line
<template>
  <div class="modal-wrapper" @click="onClose">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <div class="modal-title"><b></b></div>
          <button type="button" class="close">
            <span @click="onClose">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <ul class="basicInfo">
            <p style="font-size: 1.1rem; margin-bottom: 1%"></p>
            <li>
              {{ details.type }}
              <a v-show="details.type == office">()</a>
              <a v-show="details.type != office">()</a>
            </li>
            <li>{{ details.spaceId }}()</li>
            <li>
              {{ details.info }}
            </li>
          </ul>
          <hr />
          <ul class="basicInfo">
            
            <li>
              {{ details.location }}
            </li>
          </ul>
          <hr />
          <ul class="basicInfo">
            
            <li v-show="details.type == office">
               :       .
            </li>
            <li v-show="details.type != office">
              :   5%  
              <li v-show="details.type != office"> :   20% 
                     .</li>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    details: {
      type: Object,
      required: true,
    },
  },
  setup(props, { emit }) {
    const onClose = () => {
      emit("close");
    };

    return {
      onClose,
    };
  },
};
</script>

<style scoped>
.modal-wrapper {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-title {
  padding-left: 5%;
  font-size: 1.5rem;
}
.modal-header {
  height: 70px;
}
.basicInfo {
  padding-left: 5%;
  opacity: 0.5;
}
.modal-content {
  height: fit-content;
  /* text-align: center; */
}
.close {
  margin: 0;
}
</style>
